import React, { useState } from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import { LectureModel } from "../../dataModels/LectureModel";
import { QuestionInterviewModel } from "../../dataModels/QuestionInterviewModel";

const styles = {
  card: {
    maxWidth: "100%",
    margin: 10
  },
  title: {
    fontSize: 14
  },
  pos: {
    marginBottom: 12
  }
};

function QuestionInterview(props) {
  const { classes } = props;
  const [isShow, setIsShow] = useState(false);

  return (
    <Card className={classes.card}>
      <CardContent>
        <Typography
          className={classes.title}
          color="textSecondary"
          gutterBottom
        />
        <Typography variant="h5" component="h2">
          면접시 물어 봐야 할 것들
          <button onClick={_ => setIsShow(!isShow)}>show/hide</button>
        </Typography>

        {isShow ? (
          <ui>
            {QuestionInterviewModel.map(data => {
              return (
                <div>
                  <h3>{data.category}</h3>
                  {data.questions.map(question => (
                    <li>{question}</li>
                  ))}
                </div>
              );
            })}
          </ui>
        ) : (
          undefined
        )}
      </CardContent>
    </Card>
  );
}

QuestionInterview.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(QuestionInterview);
