import React, { useState } from "react";
import { withStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import { CheckCompanyModel } from "../../dataModels/CheckCompanyModel";
import { AvoidCompanyModel } from "../../dataModels/AvoidCompanyModel";

const styles = {
  card: {
    width: "100%",
    margin: 10
  },
  title: {
    fontSize: 14
  },
  pos: {
    marginBottom: 12
  }
};

function AvoidCompany(props) {
  const { classes } = props;
  const [isShow, setIsShow] = useState(false);

  return (
    <Card className={classes.card}>
      <CardContent>
        <Typography variant="h5" component="h2">
          이왕이면 피하는게 좋은 회사
          <button onClick={_ => setIsShow(!isShow)}>show/hide</button>
        </Typography>
        {isShow ? (
          <ui>
            {AvoidCompanyModel.map(data => {
              return (
                <li>
                  <div>{data.text}</div>
                  <div>이유: {data.reason}</div>
                </li>
              );
            })}
          </ui>
        ) : (
          undefined
        )}
      </CardContent>
    </Card>
  );
}

export default withStyles(styles)(AvoidCompany);
